  getDataFromJsonFile = (event) => {
    const reader = new FileReader();
    reader.readAsDataURL(event.target.files[0]);
    reader.onload = (FileEvents) => {
      let defPrefJSON = FileEvents.target.result;
      if (FileEvents.target.result !== undefined) {
        defPrefJSON = atob(
          defPrefJSON.split("data:application/json;base64,")[1]
        );
      }
      this.state.DocMetaDataJson = JSON.parse(defPrefJSON);
    };
  };

